.modal.open {
  opacity: 1;
  pointer-events: auto;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
}

.inactive-step {
  display: none;
}

.active-step {
  display: block;
}

body {
  background-image: url("../public/images/background3.svg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}